import { Model } from "../base/Model";
import { Component } from "../base/Component";
export class Catalog extends Model {
    constructor(data, eventKey, events) {
        super(data, events);
        this.items = data.items;
        this.loading = data.loading;
        this.eventKey = eventKey;
    }
    setItems(items) {
        this.items = items;
        this.emitChanges(this.eventKey, {
            items: this.items
        });
    }
}
export class CatalogView extends Component {
    constructor(root, events, Item, loading) {
        super(root, events);
        this.Item = Item;
        this.loading = loading;
    }
    set items(items) {
        this.container.replaceChildren(...items.map((item) => {
            const itemView = new this.Item(this.events);
            return itemView.render(item);
        }));
    }
    renderLoading() {
        this.container.replaceChildren(this.loading);
    }
}
