import { Api } from './base/api';
export class AuctionAPI extends Api {
    constructor(cdn, baseUrl, options) {
        super(baseUrl, options);
        this.cdn = cdn;
    }
    getLotItem(id) {
        return this.get(`/lot/${id}`).then((item) => (Object.assign(Object.assign({}, item), { image: this.cdn + item.image })));
    }
    getLotUpdate(id) {
        return this.get(`/lot/${id}/_auction`).then((data) => data);
    }
    getLotList() {
        return this.get('/lot').then((data) => data.items.map((item) => (Object.assign(Object.assign({}, item), { image: this.cdn + item.image }))));
    }
    placeBid(id, bid) {
        return this.post(`/lot/${id}/_bid`, bid).then((data) => data);
    }
    orderLots(order) {
        return this.post('/order', order).then((data) => data);
    }
}
