import { Component } from "../base/Component";
import { cloneTemplate, createElement, ensureElement } from "../../utils/utils";
export class lotCatalogView extends Component {
    constructor(events) {
        const template = cloneTemplate('#card');
        const btn = ensureElement('.card__action', template);
        btn.addEventListener('click', () => {
            this.events.emit('catalog.items:click', {
                id: this._id
            });
        });
        super(template, events);
        this._id = '';
    }
    set id(value) {
        this._id = value;
    }
    set datetime(value) {
        this._datetime = this.parseDate(value);
        if (this._status) {
            this.updateStatusText(this._status);
        }
    }
    set status(value) {
        this._status = value;
        const el = ensureElement('.card__status', this.container);
        const date = this._datetime;
        let text = '';
        switch (value) {
            case 'wait':
                text = date ? `  ${this.formatDate(date)}` : '  (  )';
                break;
            case 'active':
                text = date ? ` ${this.formatDate(date)}` : ' (  )';
                break;
            case 'closed':
                text = '';
                break;
        }
        this.setText(el, text);
    }
    updateStatusText(value) {
        const el = ensureElement('.card__status', this.container);
        const date = this._datetime;
        let text;
        let statusClass;
        switch (value) {
            case 'wait':
                text = date ? ` ${this.formatDate(date)}` : ' (  )';
                break;
            case 'active':
                text = date ? `  ${this.formatDate(date)}` : '  (  )';
                statusClass = 'card__status_active';
                break;
            case 'closed':
                text = date ? ` ${this.formatDate(date)}` : ' (  )';
                statusClass = 'card__status_closed';
                break;
        }
        this.setText(el, text);
        this.toggleClass(el, statusClass);
    }
    /**
     *   .
     *  :
     * - Date
     * - string ( ISO   ,   new Date(...))
     *
     * :
     * - Date,   
     * - null,     
     */
    parseDate(value) {
        if (value == null)
            return null;
        if (value instanceof Date) {
            return Number.isNaN(value.getTime()) ? null : value;
        }
        const s = value.trim();
        if (!s)
            return null;
        const d = new Date(s);
        return Number.isNaN(d.getTime()) ? null : d;
    }
    formatDate(date) {
        return date.toLocaleDateString('ru-RU', {
            day: 'numeric',
            month: 'long',
            hour: 'numeric',
            minute: 'numeric'
        });
    }
    set title(value) {
        this.setText(ensureElement('.card__title', this.container), value);
        this._title = value;
    }
    set about(value) {
        this.setText(ensureElement('.card__description', this.container), value);
        this._about = value;
    }
    set image(value) {
        this.setImage(ensureElement('.card__image', this.container), value, this._title);
        this._image = value;
    }
}
export class lotModalView extends Component {
    constructor(events) {
        const template = cloneTemplate('#lot');
        super(template, events);
        this._id = '';
        this._title = '';
        this._description = '';
        this._image = '';
        this._status = null;
        this._datetime = null;
        this._price = 0;
        this._minPrice = 0;
        this._history = [];
    }
    set id(value) {
        this._id = value;
    }
    set datetime(value) {
        this._datetime = this.parseDate(value);
        this.syncStatusText();
    }
    set status(value) {
        this._status = value;
        this.syncStatusText();
    }
    syncStatusText() {
        if (!this._status) {
            return;
        }
        this.updateStatusText(this._status);
    }
    updateStatusText(value) {
        const elDate = ensureElement('.lot__status-timer', this.container);
        const elDesc = ensureElement('.lot__status-text', this.container);
        const elForm = ensureElement('.lot__bid', this.container);
        const date = this._datetime;
        let textDate = '  ';
        let textDesc = '';
        switch (value) {
            case 'wait':
                textDate = date ? this.formatCountdown(date) : '  ';
                textDesc = '  ';
                this.setHidden(elForm);
                break;
            case 'active':
                textDate = date ? this.formatCountdown(date) : '  ';
                textDesc = '  ';
                break;
            case 'closed':
                textDate = ' ';
                textDesc = `  ${this.formatCurrency(this._price)}`;
                this.setHidden(elForm);
                break;
        }
        this.setText(elDate, textDate);
        this.setText(elDesc, textDesc);
    }
    /**
     *   .
     *  :
     * - Date
     * - string ( ISO   ,   new Date(...))
     *
     * :
     * - Date,   
     * - null,     
     */
    parseDate(value) {
        if (value == null) {
            return null;
        }
        if (value instanceof Date) {
            return Number.isNaN(value.getTime()) ? null : value;
        }
        const s = value.trim();
        if (!s) {
            return null;
        }
        const d = new Date(s);
        return Number.isNaN(d.getTime()) ? null : d;
    }
    formatCountdown(value) {
        const target = this.parseDate(value);
        if (!target) {
            return '0 0 0  0 ';
        }
        const diffMs = Math.max(0, target.getTime() - Date.now());
        const totalSec = Math.floor(diffMs / 1000);
        const days = Math.floor(totalSec / 86400);
        const hours = Math.floor((totalSec % 86400) / 3600);
        const minutes = Math.floor((totalSec % 3600) / 60);
        const seconds = totalSec % 60;
        return `${days} ${hours} ${minutes}  ${seconds} `;
    }
    set title(value) {
        this.setText(ensureElement('.lot__title', this.container), value);
        this._title = value;
    }
    set description(value) {
        const contentEl = ensureElement('.lot__content', this.container);
        const titleEl = ensureElement('.lot__title', contentEl);
        const paragraphs = value
            .split('\n')
            .map((item) => item.trim())
            .filter(Boolean);
        contentEl.querySelectorAll('.lot__description').forEach((item) => item.remove());
        const paragraphNodes = paragraphs.length
            ? paragraphs.map((item) => createElement('p', {
                className: 'lot__description',
                textContent: item
            }))
            : [createElement('p', {
                    className: 'lot__description',
                    textContent: ''
                })];
        const descFrame = document.createDocumentFragment();
        paragraphNodes.forEach((item) => descFrame.appendChild(item));
        titleEl.after(descFrame);
        this._description = value;
    }
    set image(value) {
        this.setImage(ensureElement('.lot__image', this.container), value, this._title);
        this._image = value;
    }
    set history(value) {
        this._history = Array.isArray(value) ? value : [];
        const elHistory = ensureElement('.lot__history', this.container);
        const elForm = ensureElement('.lot__bid', this.container);
        elHistory.replaceChildren();
        if (!this._history.length) {
            this.setHidden(elHistory);
            return;
        }
        else {
            this.setVisible(elHistory);
            const elHistoryTitle = createElement('span', {
                className: 'lot__history-caption',
                textContent: ' :',
            });
            const bids = createElement('ul', {
                className: 'lot__history-bids'
            });
            this._history.forEach((item) => {
                const li = createElement('li', {
                    className: 'lot__history-item',
                    textContent: String(item),
                });
                bids.appendChild(li);
            });
            elHistory.append(elHistoryTitle, bids);
        }
        if (this._status === 'closed' || this._status === 'wait') {
            this.setHidden(elForm);
            return;
        }
        else {
            this.setVisible(elForm);
            const inputEl = elForm.elements.namedItem('lot_bid');
            if (!inputEl) {
                return;
            }
            inputEl.addEventListener('input', (evt) => {
                const target = evt.target;
                const digits = target.value.replace(/\D/g, '');
                target.value = digits ? this.formatCurrency(Number(digits), false) : '';
            });
            elForm.addEventListener('submit', (e) => {
                e.preventDefault();
                const value = Number(inputEl.value);
                inputEl.value = '';
                this.events.emit('lot:placeBid', { id: this._id, value: value });
            });
        }
    }
    set price(value) {
        this._price = value;
        this.syncStatusText();
    }
    set minPrice(value) {
        this._minPrice = value;
    }
    formatCurrency(value, icon = true) {
        let options = {
            style: 'decimal',
            minimumFractionDigits: 0,
            maximumFractionDigits: 0,
        };
        if (icon) {
            options = Object.assign(Object.assign({}, options), { style: 'currency', currency: 'RUB' });
        }
        return new Intl.NumberFormat('ru-RU', options).format(value);
    }
}
